@import '_var';



body{
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
  
}

// index page
.logo{
    height: 50px;
    @media only screen and (max-width:400px){
        height: 40px;
    }

}
.blue{
    background-color: $theBlue;
}
.dropdown-menu{
    left: -78px !important;
}
.menu{

        margin: 20px;
        text-decoration: none;
        font-size: medium;
        color: black;
        &:hover{
            color: $orange;
            border-bottom: 3px solid $orange;
            text-decoration: none;
        }
 
   
}
.menu_1{
    @media only screen and (max-width:600px){
        display: none;
    }
}
.menu_2{
    display: none;
    @media only screen and (max-width:600px){
        display: block;
    }
}
// sliders
.carousel-item{
    img{
        height: 800px;
        object-fit: cover;
    }
}
.tasks{
    font-size:large;
    i{
        color: $orange;
        font-size: xx-large;
    }
}
// section 3
.bg-img{
    @media only screen and (max-width:1000px){
        flex-direction: column;
    }
    img{
        width: 50%;
        height: 400px;
        object-fit: cover;
        @media only screen and (max-width:1000px){
            width: 100%;
        }
    }
    .bg-svg{
        width: 50%;
        height: 400px;
        background-color: $orange;
        @media only screen and (max-width:1000px){
            width: 100%;
        }
        
        h1, p{
            font-weight: 700;
            text-align: center;
            margin: auto;
            line-height: 35px;
        }
    }
}
.bg-img2{
    background-image: url('../img/smolesection.jpg');
    object-fit: cover;
    

    .bg-color{
        background-color: $simpleBlue;
        color: white;
        h4{
            margin: auto;
            width: 60%;
            text-align: center;
            font-weight: 600;
            line-height: 50px;
        }
    }
}
// contact us section

.contactUs{
    font-weight: 700;
    border-bottom: 8px solid $orange;
    width: fit-content;
    margin: 30px auto;
    color: $theBlue;
}
.inputs_mail_sub{
    font-weight: 600;

    input{
        width: 100%;
        height: 50px;
        border-radius: 6px;
        border: 0.5px solid $simpleBlue;

     

    }
   
}
.description{
    font-weight: 600;
    .textarea{
        width: 100% !important;
        border-radius: 6px;
        border: 0.5px solid $simpleBlue;
    }
}
.btn-1{
    width: 150px;
    margin: 20px auto;
    
}
.contacts{
    width: 40%;
    margin: auto;

    @media only screen and (max-width:400px){
        width: 60%;
    }

}
// login pop up
.shadow{
    position: fixed;
    height: 100%;
    width: 100%;
    z-index: 100;
    background-color: rgba(0, 0, 0, 0.582);
}